.Header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 120px;

    display: flex;
    align-items: center;

    background-color: #e0ebe8;

    img {
        margin-left: 20%;

        height: 80px;
        border-radius: 100%;

        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
    }
}