
.Bio {
    padding: 0;
    background-color: white;
    position: relative;
    background-color: #e0ebe8;

    img {
        max-width: 100%;
    }

    .Bio1 {
        position: absolute;
        top: 50px;
        left: 50px;
        width: 450px;
        padding: 20px;
        background: rgba(70,85,111, 0.6);
        color: white;
        border-radius: 4px;
    }

    .Bio2 {
        position: absolute;
        bottom: 80px;
        left: 200px;
        width: 220px;
        padding: 20px;
        background: rgba(70,85,111, 0.6);
        color: white;
        border-radius: 4px;
    }
}
