.App {

}


.InnerHeader {
    text-align: center;
    padding: 20px;
    font-size: 30px;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 2px;
    font-weight: lighter;
}

.Row {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 10%;

    .title {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 10px;
    }

    img {
        max-height: 250px;
    }
}

.services {
    padding: 40px 0;
    background-color: #e0ebe8;

    hr {
        width: 80%;
    }
}

#Clases {
    text-align: center;
    img {
        max-width: 80%;
    }

    div.head {
        padding: 40px 0 20px;
        font-size: 20px;
    }

    div.desc {
        padding-bottom: 20px;
    }

    div.button {
        display: inline-block;
        padding: 10px 20px;
        background-color: #45b29a;
        border-radius: 4px;
        cursor: pointer;
        color: white;
        font-weight: bold;
        margin-bottom: 20px;
    }

    ul {
        margin-top: 0;
        display: inline-block;
        text-align: left;
    }
}